import { Checkbox } from 'antd';

import layout from './fieldLayout';

const { Group: CheckboxGroup } = Checkbox;

const options = [
  {
    label: '',
    value: 0,
  },
  {
    label: '',
    value: 1,
  },
  {
    label: '',
    value: 2,
  },
  {
    label: '',
    value: 3,
  },
];
function onChange() {}

export default function (params = {}) {
  const { uuid } = params;
  return {
    Component: CheckboxGroup,
    label: 'CheckboxGroup',
    import: 'Checkbox',
    extra: 'const { Group: CheckboxGroup } = Checkbox;',
    stateCode: `options${uuid}: ${JSON.stringify(options)}`,
    renderCode: `options${uuid}`,
    // 
    isField: true,
    fieldProps: {
      title: '',
      label: 'property',
      rules: [],
      initialValue: [2],
      ...layout,
    },
    props: {
      options,
      optionsVal: `options${uuid}`,
      onChange: onChange,
    },
  };
};



